.messageForm__wrapper {
  width: 100%;
  height: 100%;
  background-color: transparent;
  position: absolute;
}

.messageForm__inner {
  width: 100%;
  height: 45px;
  margin-top: -75px;
  position: sticky;
  top: 20px;
  display: flex;
  background-color: transparent;
}

.messageForm__textArea {
  padding-left: 15px;
  margin-left: 20px;
  width: 100%;
  font-family: inherit;
  font-size: 16px;
  border: none;
  border-radius: 50px;
  background-color: rgb(230, 229, 229);
}

.messageForm__btn {
  width: 150px;
  height: 30px;
  margin: 15px 0px 0px 5px;
  position: sticky;
  font-size: 16px;
  border-radius: 50px;
}

.messageForm__btn:hover {
  color: aliceblue;
  background-color: gold;
}
